<template>
   <div class="sg-body">
            <router-link :to="'/searchlistdetail?img='+likes.img+'&title='+likes.title+'&price='+likes.price+'&orgPrice='+likes.orgPrice+'&tradeItemId='+likes.tradeItemId+'&cfav='+likes.cfav"  tag="div">
                <img :src="likes.img">
            </router-link>
            
            <p class="firstp">
                <span>{{likes.title}}</span>
                <span class="hidden" v-for="(u,k) of likes.props" :key="k">{{u}}</span>
            </p> 
            <p class="lastp">¥ {{likes.price}}<i class="iconfont icon-shoucang"></i>
                <span>{{likes.cfav}}</span>
            </p> 
            <p class="hidd">已售{{likes.sale}}件</p>   
   </div>
</template>

<script>
 export default{
     data(){
         return {

         }
     },
     props:{
         likes:null
     },
 }
</script>

<style lang="less">
    .sg-body{
        width:47%;
        // border:1px solid red;
        margin-left:0.12rem;
        border-radius: 0.15rem;
        img{
            width:100%;          
        }
        .firstp{
            height:0.74rem;
            position: relative;
            top:-0.2rem;
            span:first-child{
                font-size: 0.2rem;
                text-indent: 0.28rem;
                color:gray;
                display: -webkit-box;
                -webkit-box-orient:vertical;
                -webkit-line-clamp: 2;
                // overflow: hidden;
                text-overflow:clip;
            }
            .hidden{
                 display:none;
                //  border:1px solid gray;
                 font-size:0.12rem;
                 background-color: rgb(204, 202, 199);
                 padding:0rem 0.05rem;
                 margin:0 0.1rem;
                 color:rgb(78, 72, 72);
             }
             
             
        }

        .lastp{
            font-size: 0.25rem;
            position: relative;
            top:-0.15rem;
            font-weight: 600;
            span{
                 color:gray;
                 float:right;
                 font-weight: 300;
             }
             i{
                 float:right;
                 color:gray;
             }
        }

        .hidd{
            display:none;
        }
    }
</style>





