const getters={
    imgs(state){
        return state.imgs;
    },
    types(state){
        return state.types;
    },
    benifits(state){
        return state.benifits;
    },
    hots(state){
        return state.hots;
    },
    likes(state){
        return state.likes;
    },
    lists(state){
        return state.lists;
    },
    tops(state){
        return state.tops;
    },
    pid(state){
        return state.pid;
    },
    bottoms(state){
        return state.bottoms;
    },
    bottomsDoc(state){
        return state.bottoms.wall? state.bottoms.wall.docs : [];
    },
    fcid(state){
        return state.fcid;
    },
    sort(state){
        return state.sort;
    },
    hotsearchs(state){
        return state.hotsearchs;
    },
    hotSearchHotword(state){
        return state.hotsearchs.hotWord?state.hotsearchs.hotWord.data:[]
    },
    hotSearchsketch(state){
        return state.hotsearchs.sketch?state.hotsearchs.sketch.data.frontword:[]
    },
    allsearch(state){
        return state.allsearch;
    },
    allsearchDoc(state){
        return state.allsearch.wall?state.allsearch.wall.docs:[]
    },
    searchsort(state){
        return state.searchsort;
    },
    searchhistory(state){
        return state.searchhistory;
    },
    historys(state){
        return state.historys;
    },
    pointsearchs(state){
        return state.pointsearchs;
    },
    title(state){
        return state.title;
    },
    cid(state){
        return state.cid;
    },
    pointsearchsDoc(state){
        return state.pointsearchs.wall? state.pointsearchs.wall.docs : [];
    },
    pointsearchsSort(state){
        return state.pointsearchs.sortFilter? state.pointsearchs.sortFilter: [];
    },
    shoppingcar(state){
        return state.shoppingcar;
    },
    shoppingcars(state){
        return state.shoppingcars?state.shoppingcars:"";
    },
    itemid(state){
        return state.itemid;
    },
    comments(state){
        return state.comments;
    },
    
}

export default getters;


