import jsonp from '../api'


const actions={
    loadWrapImg({commit,state}){
        if(state.imgs.length==0){
            jsonp.findPid({
                        appPlat:"m",
                        pids:"51822"
                    }).then(({data})=>{
                        commit("setImgs",data["51822"]["list"]);
                    })
        }
       
    },
    loadTypeImg({commit,state}){
        if(state.types.length==0){
            jsonp.findPid({
                        appPlat:"m",
                        pids:"106930"
                    }).then(({data})=>{
                        commit("setType",data["106930"]["list"]);
                    })
        }
    },
    loadBenifitsImg({commit,state}){
        if(state.benifits.length==0){
            jsonp.findPid({
                        appPlat:"m",
                        pids:"51833"
                    }).then(({data})=>{
                        commit("setBenifits",data["51833"]["list"]);
                    })
        }
    },
    loadHotsImg({commit,state}){
        if(state.hots.length==0){
            jsonp.findPid({
                        appPlat:"m",
                        pids:"51836"
                    }).then(({data})=>{
                        commit("setHots",data["51836"]["list"]);
                    })
        }
    },
    loadLikesImg({commit,state}){
        if(state.likes.length==0){
            jsonp.findPids({
                        pid:"9750",
                        sort:"pop",
                        cKey:46
                    }).then(({result})=>{
                        commit("setLikes",result.wall.docs);
                    })
        }
    },
    loadLists({commit,state}){
        if(state.lists.length==0){
            jsonp.findPid({
                       appPlat:"m",
                        pids:"41789"
                    }).then(({data})=>{
                        console.log(data);
                        commit("setLists",data["41789"]["list"]);
                        state.pid=data["41789"]["list"][0].maitKey;
                        state.fcid=data["41789"]["list"][0].miniWallkey;
                        
                    })
        }
    },
    loadRightTops({commit,state}){
            jsonp.findRightTopPids({
                       appPlat:"m",
                        pid:state.pid
                    }).then(({data})=>{
                        console.log(data);
                        commit("setRightTops",data.categoryNavigation.list);
                    })
    },
    loadRightBottoms({commit,state}){
        jsonp.findRightBottomPids({
                    page:1,
                    fcid:state.fcid,
                    cKey:"h5-cube",
                    sort:state.sort
                }).then(({result})=>{
                    console.log(result);
                    commit("setRightBottoms",result);
                })
    },
    loadHotSearchs({commit,state}){
        jsonp.findHotSearchs({
                    platform:"H5",
                    code:"hotWord,sketch"
                }).then(({data})=>{
                    console.log(data);
                    commit("setHotSearchs",data);
                })
    },
    loadAllSearchs({commit,state}){
        jsonp.findAllSearchs({
                    q:state.searchhistory,
                    cKey:46,
                    page:1,
                    sort:state.searchsort
                }).then(({result})=>{
                    console.log(result);
                    commit("setAllSearch",result);
                })
    },
    loadPointSearchs({commit,state}){
        // console.log(state.cid,state.title,state.searchsort);
        jsonp.findAllSearchs({
                    fcid:state.cid,
                    title:state.title,
                    cKey:16,
                    page:1,
                    sort:state.searchsort
                }).then(({result})=>{
                    console.log(result);
                    commit("setPointSearchs",result);
                })
    },
    loadComments({commit,state}){
        // console.log(state.cid,state.title,state.searchsort);
        jsonp.findComments({
                    type:1,
                    itemId:state.itemid,
                    appPlat:"m",
                }).then(({data:{list}})=>{
                    console.log(list);
                    commit("setComments",list);
                })
    }
}


export default actions


