import fetchJsonp from 'fetch-jsonp'


function  filterDatas(data){
    let params="";
    for(let i in data){
        params+=`${i}=${data[i]}&`
    }
    return params.substring(0,params.length-1);
}

const url = "https://mce.mogucdn.com/jsonp/multiget/3";
const url2="https://list.mogujie.com/search";
const url3="https://mce.mogujie.com/jsonp/makeup/3";
const url4=" https://list.mogujie.com/search";
const url5="https://list.mogujie.com/module/mget";
const url6="https://list.mogujie.com/search";
const url7="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";


const jsonp={
    findPid(params){
        return fetchJsonp(url+"?"+filterDatas(params)).then((response)=>{
            return response.json();
        })

    },
    findPids(params){
        return fetchJsonp(url2+"?"+filterDatas(params)).then((response)=>{
            return response.json();
        })

    },
    findRightTopPids(params){
        return fetchJsonp(url3+"?"+filterDatas(params)).then((response)=>{
            // console.log(1111);
            return response.json();
        })

    },
    findRightBottomPids(params){
        return fetchJsonp(url4+"?"+filterDatas(params)).then((response)=>{
            // console.log(1111);
            return response.json();
        })

    },
    findHotSearchs(params){
        return fetchJsonp(url5+"?"+filterDatas(params)).then((response)=>{
            // console.log(1111);
            return response.json();
        })

    },
    findAllSearchs(params){
        return fetchJsonp(url6+"?"+filterDatas(params)).then((response)=>{
            // console.log(1111);
            return response.json();
        })

    },
    findComments(params){
        return fetchJsonp(url7+"?"+filterDatas(params)).then((response)=>{
            // console.log(1111);
            return response.json();
        })

    }
}

export default jsonp


