<template>
    <div class="mbf-body">
        <div v-for="(v,i) of benifits" class="item" :key="i">
            <p>{{v.title}}<br/><span>{{v.viceTitle | bracket}}</span></p>
            <img :src="v.image">
        </div>
    </div>
</template>

<script>
     import jsonp from "fetch-jsonp"
    export default {
        data(){
            return {

            }
        },
        filters:{
            bracket(value){
                return value.substring(1,value.length-1);
            }
        },
        // methods:{
        //     filterDatas(data){
        //         let params="";
        //         for(let i in data){
        //             params+=`${i}=${data[i]}&`
        //         }
        //         return params.substring(0,params.length-1);
        //     },
        //     loadSwiperImg(){
        //         let url = "https://mce.mogucdn.com/jsonp/multiget/3";
        //         let params={
        //              appPlat:"m",
        //              pids:"51833"
        //         };
        //         jsonp(url+"?"+this.filterDatas(params)).then((response)=>{
        //             return response.json();
        //         }).then(({data})=>{
        //             console.log(data);
        //             this.datas=data[51833].list;
        //         });

        //     }
        // },
         computed:{
            benifits(){
                return this.$store.getters.benifits;
            }
        },
         mounted(){
                this.$store.dispatch("loadBenifitsImg");
        }
    }
</script>

<style lang="less">
    .mbf-body{
        margin-top: 0.2rem;
        .item{
            width:33%;
            height:2.3rem;
            float:left;
            display:flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
             border-top:1px solid #ccc;
             border-right:1px solid #ccc;
             border-bottom: 1px solid #ccc;
             img{
                 width:80%;
             }
        }
        div:first-child{
            width:49%;
            height:3.4rem;
            border-top:1px solid #ccc;
            // border-bottom:1px solid red;
            border-right:1px solid #ccc;
             border-bottom: 0px solid #ccc;
        }
         div:nth-child(2),div:nth-child(3){
            width:49%;
            height:1.7rem;
            display: flex;
            flex-direction:row;
            justify-content:space-around;
            border-top:1px solid #ccc;
             border-bottom: 0px solid #ccc;
             border-right:0px solid #ccc;
             img{
                 width:1.35rem;
                 float:right;
             }
             p{
                 text-align: left;
                 margin-left: 0.1rem;
             }
        }
         div:nth-child(6){
             border-right:0px solid #ccc;
         }
        p{
            font-size:0.15rem; 
            span{
                color:#f9446b;
            }
            text-align: center;
        }
    }
</style>


