<template>
  <div class="sld-body" >
      <div class="wrapper" ref="wrapper">
        <div class="content">
            <i class="iconfont icon-fanhui back" @click="history"></i>
            <img :src="img">
            <p>{{title}}</p>
            <p class="price">
                ￥ <span>{{price}}</span>
                <span>￥{{orgPrice}}</span>
            </p>
            <ul class="center">
                <li>
                    <img src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                    <p>72小时发货</p>
                </li>
                 <li>
                    <img src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                    <p>7天无理由退货</p>
                </li>
                 <li>
                    <img src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp">
                    <p>退货补运费</p>
                </li>
            </ul>
            <div class="comment">
                <div class="commentall">
                    <p><span>买家评价</span>
                    <span>{{comments.length}}</span>
                    <span>| 销量</span>
                    <span>{{cfav}}</span>
                    </p>
                    <p>></p>
                </div>
                <div class="commentlist" v-for="(v,i) of comments" :key="i">
                    <div>
                        <div>
                            <img :src="v.user.avatar">
                        </div>                           
                        <p>{{v.user.uname}}</p>
                    </div>                       
                    <p>{{v.content}}</p>
                    <div class="style">
                        <p class="time">{{v.time}}</p>
                        <p class="sty">{{v.style}}</p>
                    </div>
                    <ul  v-if="v.images.length!=0">
                        <li v-for="(a,k) of v.images" :key="k">
                            <img :src="a">
                        </li>
                    </ul>
                   
                </div>
            </div> 
        </div>
    </div>
        <ul class="bottom">
            <li><i class="iconfont icon-shoucang"></i></li>
            <li>小店</li>
            <!-- <router-link :to="'/simple/simpleshoppingcar?title='+title+'&img='+img+'&price='+price+'&orgPrice='+orgPrice" tag="li">加入购物车</router-link> -->
            <li @click="admit">加入购物车</li>
            <li>立即购买</li>
        </ul>
           
   
     
  </div>
</template>
<script>
    import Bscroll from 'better-scroll'
    export default {
        computed:{
            img(){
                return this.$route.query.img
            },
            title(){
                return this.$route.query.title
            },
            price(){
                return this.$route.query.price
            },
            orgPrice(){
                return this.$route.query.orgPrice
            },
            tradeItemId(){
                return this.$route.query.tradeItemId
            },
            cfav(){
                 return this.$route.query.cfav
            },
            comments(){
                return this.$store.getters.comments
            },
            shoppingcars(){
                return this.$store.getters.shoppingcars
            }
            
        },
        mounted(){
            this.initScroll();
            console.log(this.$route.query.img,this.$route.query.title,this.$route.query.price,this.$route.query.orgPrice,this.$route.query.tradeItemId);
            this.$store.commit("setItemId",this.$route.query.tradeItemId);
            this.$store.dispatch("loadComments");
        },
        methods:{
            initScroll(){
                this.$nextTick(()=>{
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll1.refresh();
                    }
                });
            },
            history(){
               this.$router.back(-1);
            },
            admit(){        
                // 把当前页面信息存到仓库中       
                this.$store.commit("setShoppingCar",{title:this.title,img:this.img,price:this.price,orgPrice:this.orgPrice,tradeItemId:this.tradeItemId,count:1});
                // 判断是否已经加入购物车
                if(this.shoppingcars.length!=0){
                    let own=true;
                    for(let i=0;i<this.shoppingcars.length;i++){
                        if(this.shoppingcars[i].tradeItemId==this.$store.getters.shoppingcar.tradeItemId){
                            this.shoppingcars[i].count++;
                            console.log(11111);
                            own=false;
                            break;
                        }
                    }
                    if(own){
                        this.shoppingcars.push(this.$store.getters.shoppingcar);
                    }
                }else{
                    // 将当前页面信息追加到仓库中存放购物车所以信息的数组中
                    this.shoppingcars.push(this.$store.getters.shoppingcar);
                }
                this.$store.commit("setShoppingCars",this.shoppingcars);
                // 点击事件中实现路由跳转，并将仓库中的存放当前页面信息传递过去
                this.$router.push({name:'simpleshoppingcar',
                                    params:{title:this.title,img:this.img,price:this.price,orgPrice:this.orgPrice}});

                
            }
        }
    }
</script>

<style lang="less">
    .sld-body{
        overflow: hidden;
        .wrapper{
            position: absolute;
            top:0rem;
            bottom:1rem;
            left:0rem;
            right:0rem;
            .content{
                .center{
                    width:100%;
                    height:0.6rem;
                    display: flex;
                    border-bottom:6px solid rgb(218, 213, 213);
                    li{
                        width:33.33%;
                        padding:0.1rem 0.2rem;
                        display:flex;
                        font-size: 0.12rem;
                        color:gray;
                        text-align: center;
                        // border:1px solid red;
                        img{
                            width:0.35rem;
                            height:0.35rem;
                        }
                        p{
                            padding:0.05rem 0.05rem 0;
                        }
                    }
                }
            }
            .back{
                position: absolute;
                font-size: 0.5rem;
                color: white;
            }
            img{
                width:100%;
            }
            p:nth-child(3){
                height:0.8rem;
                font-size:0.3rem;
                padding-left:0.2rem;
                line-height: 0.4rem;
            }
            .price{
                padding-left:0.2rem;
                height:1rem;
                line-height: 0.7rem;
                font-size:0.2rem;

                span:first-child{
                    font-size:0.45rem;
                }
                 span:last-child{
                    font-size:0.2rem;
                    position: relative;
                    top:-0.12rem;
                    left:0.1rem;
                    text-decoration:line-through;
                }
                
            }
         
        }
        .comment{
            margin-top:0.2rem;
            margin-left:0.2rem;   
            .commentall{
                font-size:0.12rem;
                display:flex;
                justify-content: space-between;
                 color:gray; 
            }
            .commentlist{
                width:100%;
                padding:0.2rem 0.1rem;
                // border:1px solid red;
                font-size:0.2rem;
                color:rgb(105, 101, 101);
                div:first-child{
                    display:flex;
                    div:first-child{
                        width:0.5rem;
                        height:0.5rem;
                        border-radius:0.25rem;
                        margin-right:0.1rem;
                    }
                    p{
                       margin-top:0.1rem; 
                       
                    }
                    
                }
                .style{
                    display:flex;
                    .time{
                     font-size:0.2rem;
                    }
                    .sty{
                        float:left;
                        margin-left: 0.3rem;
                    }
                }
               ul{
                   width:100%;
                   height:0.9rem;
                   display:flex;
                   margin-top:0.1rem;
                   li{
                       width:20%;
                       height:100%;
                       margin-right:0.1rem;
                      img{
                          width:100%;
                          height:100%;
                      }

                   }
               }
            }
            
        }
           .bottom{
                width:100%;
                height:0.9rem;
                display:flex;
                font-size: 0.28rem;
                //  border:1px solid red;
                 text-align: center;
                 line-height: 1rem;
                 position:fixed;
                 bottom:0rem;
                 ul{
                     border-bottom:0.2rem solid gray;
                 }
                li:first-child{
                    width:15%;
                    height:100%;
                    // border:1px solid red;
                    i{
                        font-size:0.4rem;
                    }
                }
                 li:nth-child(2){
                    width:19%;
                    height:100%;
                    // border:1px solid red;
                }
                li:nth-child(3){
                    width:33%;
                    height:100%;
                    // border:1px solid red;
                    color:rgb(243, 61, 106);
                    background:rgb(248, 202, 212)
                }
                li:nth-child(4){
                    width:33%;
                    height:100%;
                    // border:1px solid red;
                    background: rgb(243, 61, 106);
                    color:white;
                }

            }
    }
</style>




