<template>
    <div class="spd-body">
        <div class="sd-top">
            <i class="iconfont icon-fanhui picture" @click="history"></i>
            <div>{{title}}</div>
            <i class="iconfont icon-gouwuche picture"></i>
        </div>

         <div class="wrapper" >
                <div class="menu">
                    <div v-for="(v,i) of pointsearchsSort" :key="i" @click="changeSort(v.sortKey)" :class="v.sortKey|select(searchsort)">{{v.title}}</div>
                </div>               
                 <div class="content" ref="wrapper">    
                    <div class="list">
                        <Single :likes="pointsearchsDoc[i]" v-for="(v,i) of pointsearchsDoc" :key="i"></Single>
                    </div>
                </div>
        </div>
        
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
    export default {
        methods:{
            history(){
               this.$router.back(-1);
            },
            initScroll(){
                this.$nextTick(()=>{
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll.refresh();
                    }
                });
            },
            changeSort(sortkey){
                this.$store.commit("setSearchSort",sortkey);
            }

        },
         filters:{
            select(value,sort){
                return value==sort?"checked":""
            }
        },
        watch:{
            // pointsearchsDoc(){
            //     console.log(111);
            //     return this.$store.getters.pointsearchsDoc;
            // },
            // pointsearchsSort(){
            //     return this.$store.getters.pointsearchsSort;
            // }
             searchsort(){
                this.$store.dispatch("loadPointSearchs");
            }
        },
        computed:{
            pointsearchsDoc(){
                return this.$store.getters.pointsearchsDoc;
            },
            pointsearchsSort(){
                return this.$store.getters.pointsearchsSort;
            },
            title(){
                return this.$store.getters.title;
            },
            searchsort(){
                 return this.$store.getters.searchsort;
            }
        },
        mounted(){
            this.initScroll();
            console.log(this.pointsearchsDoc);
        }
    }
</script>

<style lang="less">
.spd-body{
     font-size: 0.24rem;
    .sd-top{
            display:flex;
            margin-top:0.2rem;
            .picture{
                font-size: 0.4rem;
                flex-grow: 1;
                text-align: center;
             }
            div{
                width:2rem;
                height:0.58rem;
                flex-grow: 8;
                border-radius: 0.15rem;
                padding-left: 0.2rem;
                line-height: 0.58rem;
                color:rgb(36, 33, 33);
                text-align: center;
                font-size:0.28rem;
            }
        }
         .menu{
            width:100%;
            height:0.7rem;
            border-top:1px solid gray;
            border-bottom:1px solid gray;
            display:flex;
            margin-top:0.1rem;

            div{
                width:33.3%;
                height:0.38rem;
                text-align: center;
                 border-right:1px solid gray;
                 margin-top:0.14rem;
            }
            div:last-child{
                border-right:0px solid red;
            }
         .checked{
                     color:red;
                 }


        }
        .content{
            overflow: hidden;
            position: absolute;
            top:1.65rem;
            bottom:0rem;
            left:0rem;
            right:0rem;
            .list{
                display:flex;
                flex-wrap: wrap;
                 .firstp{
                     margin-top: 0.2rem;
                      span:first-child{
                       display:none;
                    }
                    .hidden{
                        display:inline-block;
                    }
                 }
            }
        }
        
}
</style>





