<template>
    <div class="mbt-body">
       <div v-for="(v,i) of types" :key="i">
           <img :src="v.image">
           <p>{{v.title}}</p>
       </div>
    </div>
</template>

<script>
      import jsonp from "fetch-jsonp"
    export default {
        data(){
            return {
              
            }
        },
        computed:{
            types(){
                return this.$store.getters.types;
            }
        },
         mounted(){
                this.$store.dispatch("loadTypeImg");
        }
    }
</script>

<style lang="less">
    .mbt-body{
         display: flex;
        // justify-content:  space-around ;
        flex-wrap:wrap;
        div{
            width:20%;
            height:10%;
            // border:1px solid red;
            text-align: center;
             margin-top:0.2rem;
        }
        img{
            width:80%;
            height:63%;
        }
        p{
            font-size:0.24rem;
            color:rgb(160, 156, 156);
        }
    }
</style>


