<template>
    <div class="mb-body wrapper" ref="wrapper">  
        <div class="content">
            <MainBodySwiper></MainBodySwiper>
            <MainBodyType></MainBodyType>
             <div class="bgimg">
                <p>超实惠-促销直达</p>
            </div>
            <MainBodyBerefit></MainBodyBerefit>
            <div class="bgimg">
                <p>热门分类</p>
            </div>
            <MainBodyHot></MainBodyHot>
             <div class="bgimg">
                <p>猜你喜欢</p>
            </div>
            <MainBodyLikes></MainBodyLikes>
        </div>            
    </div>
</template>

<script>
import Bscroll from 'better-scroll'
import jsonp from 'fetch-jsonp'

import MainBodySwiper from './MainBodySwiper'
import MainBodyType from './MainBodyType'
import MainBodyBerefit from './MainBodyBerefit'
import MainBodyHot from './MainBodyHot'
import MainBodyLikes from './MainBodyLikes'

    export default {
        components:{
            MainBodySwiper,
            MainBodyType,
            MainBodyBerefit,
            MainBodyHot,
            MainBodyLikes
        },
        methods:{
            initScroll(){
                // console.log(this.scroll);
                this.$nextTick(()=>{
                    // this.scroll = new Bscroll(this.$refs.wrapper,{});
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll.refresh();
                    }
                });
            }
        },
       mounted(){
          this.initScroll();
       }

    }
</script>

<style lang="less">
    .mb-body{
        position: absolute;
        top:0.9rem;
        left:0rem;
        right:0rem;
        bottom:0.9rem;
    }
    .bgimg{
        width:100%;
        // height:0.14rem;
        // border: 1px solid red;
        background: url(https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png) no-repeat center;
        background-size: 66% 50%;
        margin-top:0.5rem;
        margin-bottom:0.3rem;
        p{
            font-size: 0.24rem;
            
        }
        text-align: center;
        
    };

</style>




