const mutations={
    setImgs(state,data){
        state.imgs=data;
    },
    setType(state,data){
        state.types=data;
    },
    setBenifits(state,data){
        state.benifits=data;
    },
    setHots(state,data){
        state.hots=data;
    },
    setLikes(state,data){
        state.likes=data;
    },
    setLists(state,data){
        state.lists=data;
    },
    setRightTops(state,data){
        state.tops=data;
    },
    setPid(state,data){
        state.pid=data;
    },
    setRightBottoms(state,data){
        state.bottoms=data;
    },
    setFcid(state,data){
        state.fcid=data;
    },
    setSort(state,data){
        state.sort=data;
    },
    setHotSearchs(state,data){
        state.hotsearchs=data;
    },
    setSearchHistory(state,data){
        state.searchhistory=data;
    },
    setHistorys(state,data){
        state.historys=data;
    },
    setAllSearch(state,data){
        state.allsearch=data;
    },
    setSearchSort(state,data){
        state.searchsort=data;
    },
    // deleteSame(element, index, array) {
    //     return array.indexOf(element) === index;
    // }
    deleteSame(state,arr){
        let newarr =  arr.filter(function (element, index, array) {
            return array.indexOf(element) === index;
        });
        //filter返回一个新数组
        // console.log(newarr,arr);
        state.historys=newarr;
    },
    setPointSearchs(state,data){
        state.pointsearchs=data;
    },
    setTitle(state,data){
        state.title=data;
    },
    setCid(state,data){
        state.cid=data;
    },
    setShoppingCar(state,data){
        console.log(4444);
        state.shoppingcar=data;
    },
    setShoppingCars(state,data){
        state.shoppingcars=data;
    },
    setItemId(state,data){
        state.itemid=data;
    },
    setComments(state,data){
        state.comments=data;
    },
    

}

export default mutations;


