<template>
    <div class="mlr-body wrapper" ref="wrapper">
        <div class="content">
            <MainListRightTop></MainListRightTop>
            <!-- <MainListRightCenter></MainListRightCenter> -->
            <MainListRightBottom></MainListRightBottom>    
        </div>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
    import MainListRightBottom from './MainListRightBottom'
    import MainListRightTop from './MainListRightTop'
    // import MainListRightCenter from './MainListRightCenter'

    export default{
         components:{
            MainListRightTop,
            // MainListRightCenter,
            MainListRightBottom
        },
        methods:{
            initScroll(){
                // console.log(this.scroll);
                this.$nextTick(()=>{
                    // this.scroll = new Bscroll(this.$refs.wrapper,{});
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll.refresh();
                    }
                });
            }
        },
         mounted(){
                this.initScroll();
        }
        
    }
</script>

<style lang="less">
   .mlr-body{
       width:74%;
       position: absolute;
       top:0.9rem;
       bottom:0.9rem;
       right:0;
   }
</style>




