<template>
    <div class="mbs-body">
        <swiper :options="swiperOption" ref="mySwiper">
            <swiper-slide v-for="(v,i) of imgs" :key="i">
                <img :src="v.image_800">
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
    import jsonp from "fetch-jsonp"
    export default {
        data(){
            return {                
                swiperOption:{
                    autoplay:2000,
                    loop:true,
                    pagination:".swiper-pagination"
                }
            }
        },
        computed:{
            imgs(){
                return this.$store.getters.imgs;
            }
        },
         mounted(){
            this.$store.dispatch("loadWrapImg");
        }
    }
</script>

<style lang="less">
   .mbs-body{
       img{
           width:100%;
       }
   }
</style>


