<template>
    <div class="sd-body">
        <div class="sd-top">
            <i class="iconfont icon-fanhui picture" @click="history"></i>
            <router-link to="/search" class="search" tag="div">
                <i class="iconfont icon-sousuo"></i>{{searchtitle}}
            </router-link>
            <i class="iconfont icon-gouwuche picture"></i>
        </div>
       
        <div class="wrapper" >
                <div class="menu">
                    <div v-for="(v,i) of allsearch.sortFilter" :key="i" @click="changeSort(v.sortKey)" :class="v.sortKey|select(searchsort)">{{v.title}}</div>
                </div>               
                 <div class="content" ref="wrapper">    
                    <div class="list">
                        <Single :likes="allsearchs[i]" v-for="(v,i) of allsearchs" :key="i"></Single>
                    </div>
                </div>
        </div>
        
        
   
</div>
</template>

<script>
import Bscroll from 'better-scroll'
    export default {
        methods:{
            history(){
               this.$router.back(-1);
            },
            initScroll(){
                this.$nextTick(()=>{
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll1.refresh();
                    }
                });
            },
            changeSort(sortkey){
                this.$store.commit("setSearchSort",sortkey);
            }
        },
        filters:{
            select(value,sort){
                return value==sort?"checked":""
            }
        },
        watch:{
            searchsort(){
                this.$store.dispatch("loadAllSearchs");
            }
        },
        computed:{
            searchtitle(){
                return this.$route.params.title;
            },
            allsearchs(){
                return this.$store.getters.allsearchDoc;
            },
            allsearch(){
                return this.$store.getters.allsearch;
            },
            searchsort(){
                 return this.$store.getters.searchsort;
            }
        },
        mounted(){
            this.initScroll();
            // console.log(this.$route.params);
        }
    }
</script>

<style lang="less">

    .sd-body{
        font-size: 0.24rem;
        // height:0.9rem;
        // border:1px solid gray;
        justify-content:space-around;
        align-items: center;
         overflow: hidden;
        .sd-top{
            display:flex;
            margin-top:0.2rem;
            .picture{
                font-size: 0.4rem;
                flex-grow: 1;
                text-align: center;
             }
            .search{
                width:2rem;
                height:0.58rem;
                background-color: #eee;
                flex-grow: 8;
                border-radius: 0.15rem;
                padding-left: 0.2rem;
                line-height: 0.58rem;
                color:rgb(36, 33, 33);
            }
        }

        .menu{
            width:100%;
            height:0.7rem;
            border-top:1px solid gray;
            border-bottom:1px solid gray;
            display:flex;
            margin-top:0.1rem;

            div{
                width:33.3%;
                height:0.38rem;
                text-align: center;
                 border-right:1px solid gray;
                 margin-top:0.14rem;
            }
            div:last-child{
                border-right:0px solid red;
            }
         .checked{
                     color:red;
                 }


        }
        .content{
            overflow: hidden;
            position: absolute;
            top:1.65rem;
            bottom:0rem;
            left:0rem;
            right:0rem;
            .list{
                display:flex;
                flex-wrap: wrap;
                 .firstp{
                     margin-top: 0.2rem;
                      span:first-child{
                       display:none;
                    }
                    .hidden{
                        display:inline-block;
                    }
                 }
            }
        }
        
        
       
    }
</style>




