<script>
import Bscroll from 'better-scroll'
export default {
    methods:{
         initScroll(){
                this.$nextTick(()=>{
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll1.refresh();
                    }
                });
            },
    },
    mounted(){
        this.initScroll();
    }
}
</script>


<style lang="less">
.smi-body{
         overflow: hidden;
           position: absolute;
            top:0rem;
            bottom:0.9rem;
            left:0rem;
            right:0rem;
    
         ul{
          font-size: 0.28rem;
        li{
            width:100%;
            height:1rem;
            // border:1px solid red;
            display:flex;
            line-height:1rem;
            margin-left:0.25rem;
            img{
                width:0.5rem;
                height:0.5rem;
                position: relative;
                top:0.2rem;
            }
            p{
                width:85%;
                height:98%;
                border-bottom:1px solid gray;
                 margin-left:0.2rem;
            }
        }
        li:first-child{
            height:1.5rem;
            div{
                width:80%;
                display: flex;
                flex-direction:column; 
                font-size: 0.3rem;
                height:1.5rem;
