                    height:1.2rem;
                    border-radius:0.6rem;
                }
           
        }
        li:last-child{
            margin:0.2rem 0.4rem;
            button{
                 width:90%;
                 color:white;
                 font-size:0.3rem;
                background-color: rgb(247, 47, 80);
            }
           
        }
    }
}
   
</style>





