<template>
    <div class="s-body">
        <div class="top">
            <i class="iconfont icon-fanhui" @click="history"></i>
            <!-- <div class="s-search">持久防晒妆前乳</div> -->
            <input type="text" :placeholder="hotSearchsketch" class="s-search" v-model="searchHistory">
            <!-- <router-link :to="'/searchdetails?title='+searchHistory+'&sortFilter='+allsearch" @click="sea" tag="p">搜索</router-link> -->
             <!-- <router-link :to="{name:'searchdetails',params:{title:searchHistory}}" @click="sea" tag="p">搜索</router-link> -->
              <p  @click="sea">搜索</p>
            <!-- <p @click="sea">搜索</p>  -->
        </div>
        <div class="history">
            <ul>
                <li><i class="iconfont icon-sousuo"></i>历史搜索</li>
                <li><i class="iconfont icon-shanchu" @click="deleteSearch"></i></li>         
            </ul>
            <P v-show="historys.length==0">暂无历史搜索</P>
            <div v-for="(v,i) of historys" :key="i" class="sousuo" @click="addIntoSearch(v)">{{v}}</div>
        </div>
        <hr>
        <div class="hot">
            <p><i class="iconfont icon-shoucang">热门搜索</i></p>
            <div v-for="(v,i) of hotSearchHotword" :key="i" :style="'color:'+v.color" @click="addHistory(v.frontword)">{{v.frontword}}</div>
        </div>       
    </div>
</template>

<script>
    export default {
        data(){
            return {
                //  historys:[],
                // historys:[]
            }
        },
         methods:{
            sea(){
                // console.log(this.searchHistory);
                console.log(1111)
                this.historys.push(this.searchHistory);
                this.$store.dispatch("loadAllSearchs");
                this.$router.push({name:'searchdetails',params:{title:this.searchHistory}});
                // this.$store.commit("setHistorys",[...new Set(this.historys)]);
                this.$store.commit("deleteSame",this.historys);
            },
            history(){
               this.$router.back(-1);
            },
            addHistory(value){
                console.log(value);
                this.historys.push(value);
                this.$store.commit("setSearchHistory",value);
                this.$store.dispatch("loadAllSearchs");
                // 路由和点击事件冲突
                this.$router.push({name:'searchdetails',params:{title:value}});
                this.$store.commit("deleteSame",this.historys);

            },
            deleteSearch(){
                // console.log(this.historys);
                this.$store.commit("setHistorys",[]);
            },
            addIntoSearch(value){
                this.$store.commit("setSearchHistory",value);
                this.$store.dispatch("loadAllSearchs");
                this.$router.push({name:'searchdetails',params:{title:value}});
                this.$store.commit("deleteSame",this.historys);
            }
        },
        watch:{
            searchHistory(){
                // console.log(111);
                 return this.$store.getters.searchhistory;
                 
            },
            allsearch(){
                return this.$store.getters.allsearch;
            },
            historys(){
                console.log(111);
                 return this.$store.getters.historys;
            }
        },
       computed:{
           hotSearchHotword(){
               return this.$store.getters.hotSearchHotword;
           },
           hotSearchsketch(){
               return this.$store.getters.hotsearchs.hotSearchsketch;
           },
            searchHistory:{
                get(){
                     return this.$store.getters.searchhistory
                },
                set(aa){
                     this.$store.commit("setSearchHistory",aa);
                }
               
            },
            historys(){
               return this.$store.getters.historys;
           },
            allsearch(){
                return this.$store.getters.allsearch;
            }
       },
        mounted(){
                this.$store.dispatch("loadHotSearchs");
                console.log(this);
        }
    }
</script>

<style lang="less">
    .s-body{  
        color:rgb(49, 47, 47);    
        .top{
            display:flex;
            justify-content: space-around;
            font-size:0.24rem;
            margin-top:0.1rem;
            height:0.6rem;
            border-bottom:1px solid lightgray;
            color:gray;
            padding-top:0.1rem;
            .s-search{
            width:70%;
            height:0.4rem;
            border:1px solid red;
            outline: none;
            border-radius: 0.1rem;
            text-indent: 0.1rem;
        }
            i{
                font-size:0.4rem;
            }
        }

        .hot{
            width:100%;
            font-size:0.24rem;
            margin-left:0.1rem;
            div{
                padding:0 0.15rem;
                border:1px solid lightgray;
                text-align: center;
                float:left;
                margin-left:0.24rem;
                margin-top:0.2rem;
                border-radius: 0.05rem;
            }
            p{
                font-size:0.24rem;
            }
        }
        .history{
            height:1.4rem;
            font-size:0.24rem;
            margin-left:0.1rem;
            margin-top:0.16rem;
            // border:1px solid gray;
            .sousuo{
                padding:0 0.15rem;
                border:1px solid lightgray;
                text-align: center;
                float:left;
                margin-left:0.24rem;
                margin-top:0.2rem;
                border-radius: 0.05rem;
            }
            ul{
                width:96%;
                display:flex;
                justify-content:space-between;
            }
        }
      i{
          font-size:0.24rem;
          color:gray;
      }
        
    }
</style>




