import Vue from 'vue'
import Router from 'vue-router'
import MainContent from '../components/MainContent'
import MainBody from '../components/MainBody'
import MainList from '../components/MainList'
import MainListRight from '../components/MainListRight'

import SimpleContent from '../components/SimpleContent'
import SimpleMyInfo from '../components/SimpleMyInfo'
import SimpleShoppingCar from '../components/SimpleShoppingCar'



import Search from '../components/Search'
import SearchDetails from '../components/SearchDetails'
import SearchListDetail from '../components/SearchListDetail'
import SearchPointDetails from '../components/SearchPointDetails'

Vue.use(Router)

export default new Router({
  mode:"history",
  linkExactActiveClass:"active",
  routes: [
    {path:"/",redirect:"/main"},
    {path:"/main",component:MainContent,children:[
      {path:"",redirect:"body"},
      {path:"body",name:"body",component:MainBody},
      {path:"list",name:"list",component:MainList},
    ]},
    {path:"/simple",component:SimpleContent,children:[
      // {path:"",redirect:"simpleshoppingcar"},
      {path:"simpleshoppingcar",name:"simpleshoppingcar",component:SimpleShoppingCar},
      {path:"simplemyinfo",name:"simplemyinfo",component:SimpleMyInfo},
    ]},
    {path:"/search",name:"search",component:Search},
    {path:"/searchdetails",name:"searchdetails",component:SearchDetails},
    {path:"/searchlistdetail",name:"searchlistdetail",component:SearchListDetail},
    {path:"/searchpointdetails",name:"searchpointdetails",component:SearchPointDetails},
    
  ]
})



