<template>
    <div class="mll-body wrapper" ref="wrapper">
       <ul class="content">
           <li v-for="(v,i) of lists" :key="i" tag="li" @click="rightTop(v.maitKey,v.miniWallkey)" :class="v.maitKey|isselect(pid)">
               <p>{{v.title}}</p>
           </li>
       </ul>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'
     export default {
        data(){
            return {
                
            }
        },
        filters:{
            isselect(value,data){
                return value==data?"selected":""
            }
        },
        watch:{
            pid(){
                this.$store.dispatch("loadRightTops");
            },
            fcid(){
                this.$store.dispatch("loadRightBottoms");
            }
        },
         computed:{
           lists(){
                return this.$store.getters.lists;
            },
            pid(){
                return this.$store.getters.pid;
            },
            fcid(){
                return this.$store.getters.fcid;
            }
        },
         methods:{
            initScroll(){
                // console.log(this.scroll);
                this.$nextTick(()=>{
                    // this.scroll = new Bscroll(this.$refs.wrapper,{});
                    if(!this.scroll){
                        this.scroll = new Bscroll(this.$refs.wrapper,{click:true});
                    }else{
                        this.scroll.refresh();
                    }
                });
            },
            rightTop(id,bid){
                this.$store.commit("setPid",id);
                this.$store.commit("setFcid",bid);
                
            }
        },
         mounted(){
                this.initScroll();
                this.$store.dispatch("loadLists");
                // this.$store.state.pid=this.$store.state.lists[0]["maitKey"];
        }

    }
</script>

<style lang="less">
    .mll-body{
        width:25%;
        position: absolute;
        top: 0.9rem;
        bottom: 0.9rem;
        ul{
            width:100%;
            // margin-left: 0.15rem;
            // margin-top:0.1rem;
            li{
                font-size: 0.24rem;
                height:0.9rem;
                text-align: center;
                // border:1px solid red;
                 background-color: rgb(236, 236, 236);
                 p{
                     width:100%;
                     height:35%;
                    //  border-left:1px solid red;
                     margin-top:18%;
                 }
            }
            .selected{
                    color:rgb(245, 84, 125);
                    font-weight: 900;
                    background-color:white;
                    font-size:0.24rem;
                    p{
                         border-left:0.04rem solid rgb(245, 84, 125);
                         height:40%;
                    }
            }
        }
    }
</style>




