<template>
    <div class="mrb-body">
        <hr/>
        <ul>
            <li v-for="(v,i) of lines" :key="i" @click="changeNav(v.sortKey)" :class="v.sortKey|select(sort)">{{v.title}}</li>
        </ul>
        <hr/>
        <!-- <div v-for="(v,i) of bottomlists" :key="i">
            <img :src="v.img">
            <p>{{v.title}}</p>
            <p>{{v.price}}</p>
        </div> -->
        <div class="s">
            <Single :likes="bottomlists[i]" v-for="(v,i) of bottomlists" :key="i"></Single>
        </div>
        
    </div>
</template>

<script>
 export default {
        data(){
            return {
                
            }
        },
        filters:{
            select(value,sort){
                return value==sort?"checked":""
            }
        },
        watch:{
            sort(){
                this.$store.dispatch("loadRightBottoms");
            }
        },
        methods:{
            changeNav(sortkey){
                this.$store.commit("setSort",sortkey);
            }
        },
         computed:{
           bottomlists(){
            //    console.log(this.$store.getters.bottomsDoc+"====<")
                return this.$store.getters.bottomsDoc;
            },
            lines(){
                return this.$store.getters.bottoms.sortFilter;
            },
            sort(){
                 return this.$store.getters.sort;
            }
        },
        
         mounted(){
                // this.$store.dispatch("loadLists");
                console.log("12344"+this.bottomlists);
        }

    }
</script>

<style lang="less">
.mrb-body{
     margin-top:0.2rem;
    .s{
        display:flex;
        flex-wrap: wrap; 
        margin-top:0.1rem;
        .lastp{
            color:rgb(245, 84, 125);
        }
        .firstp{
             display: -webkit-box;
            -webkit-box-orient:vertical;
            -webkit-line-clamp: 1;
            // overflow: hidden;
             text-overflow:clip;
             font-size: 0.24rem;
        }
        .hidd{
            width:60%;
            height:0.38rem;
            background-image:linear-gradient(to right,rgb(26, 21, 21),rgb(245, 241, 241));
            font-size: 0.24rem;
            text-indent: 0.1rem;
            position: relative;
            top:-1.7rem;
            color:white;
            line-height: 0.38rem;
             display: -webkit-box;
            -webkit-box-orient:vertical;
            -webkit-line-clamp: 1;
            // overflow: hidden;
             text-overflow:clip;
            //  display: inline-block;
            
        }
    }
    .sg-body{
        width:46%;
        
    }
    ul{
        width:100%;
        height:0.4rem;
        display:flex;
        font-size: 0.24rem;
        color:gray;
        // border:1px solid red;
        margin-top:0.18rem;
        margin-bottom: 0.18rem;
        li{ 
            width:33.3%;
            height:0.3rem;
            text-align: center;
            border-left:1px solid gray;
        }
        li:first-child{
             border-left:0px solid gray;
        }
        .checked{
            color:rgb(245, 84, 125);
        }
    }
}
  
   
</style>




