<template>
   <div class="ssc-body">
       <div class="top">
            <i class="iconfont icon-fanhui picture" @click="back"></i>
            <div class="shop">购物车(<span>{{shoppingcars.length}}</span>)</div>
            <div>编辑 <i class="iconfont icon-xiaoxi picture"></i></div>
       </div>

       <div class="list" v-for="(v,i) of shoppingcars" :key="i">
           <div class="left"><input type="checkbox" class="check" v-model="v.checked" :class="v.checked?'add':'delete'" @change="checkSelect"></div>
           <div class="right">
               <div class="img">
                   <img :src="v.img">
               </div>
               <div class="body">
                   <p>{{v.title}}</p>
                   <p>
                       ¥<span>{{v.price}}</span>
                       <span>¥<del>{{v.orgPrice}}</del></span>
                       <span>✖️{{v.count}}<h6></h6></span></p>
               </div>
           </div>
       </div>
       
        <ul class="bottom">
            <li><input type="checkbox" class="check" v-model="all" :class="all?'add':'delete'"  @change="changeSelect"></li>
            <li>全选<span>({{num}})</span></li>
            <li>¥<span>{{totalprice}}</span></li>
            <li><input type="button" value="去结算"></li>
        </ul>
   </div>
</template>

<script>
export default {
    data(){
        return {
            all:false,
            num:0,
            totalprice:0,
            // arr:[]
        }
    },
    methods:{
        back(){
            this.$router.back(-1);
        },
         changeSelect(){
                this.num=0;
                this.totalprice=0;
                this.shoppingcars.forEach(u => {
                    u.checked = this.all;
                     if(u.checked){
                        this.num=this.shoppingcars.length;
                        this.totalprice+=(u.price*u.count);
                    }    
                   
                });
        },
         checkSelect(){
                this.all = this.shoppingcars.length!=0;
                this.num=0;
                this.totalprice=0;
                for (let i = 0; i < this.shoppingcars.length; i++) {
                    if(this.shoppingcars[i].checked){
                        this.num++;
                        this.totalprice+=(this.shoppingcars[i].price*this.shoppingcars[i].count);
                        continue;
                    }                  
                    this.all = this.shoppingcars[i].checked;
                }
                
                 
            }
    },
    computed:{
        shoppingcars(){
            return this.$store.getters.shoppingcars;
        },
        
    },
    mounted(){
        console.log(this.$route.params);
         for (let i = 0; i < this.shoppingcars.length; i++) {
                    if(this.shoppingcars[i].checked){
                        this.num++;
                        this.totalprice+=(this.shoppingcars[i].price*this.shoppingcars[i].count);
                        // continue;
                    }
                    // this.all = this.shoppingcars[i].checked;
                }
    }
}
</script>


<style lang="less">
    .ssc-body{
        .top{
            display:flex;
            font-size:0.22rem;
            height:0.8rem;
            width:100%;
             border-bottom:1px solid gray;
             line-height: 0.8rem;
            .shop{
                width:75%;;
                // border:1px solid red;
                text-align: center;
                // margin-top: 0.1rem;
                font-size:0.28rem;
            }
            i{
                font-size: 0.21rem;
            }
        }
        .list{
            width:100%;
            height:2rem;
            // border:1px solid red;
            display:flex;
            padding:0.22rem 0rem;
            .left{
                width:10%;
                padding-left:4%;
                input{
                    -webkit-appearance: none;
                    width:0.36rem;
                    height:0.36rem;
                    border:1px solid gray;
                    border-radius: 0.18rem;
                    outline: none;
                    
                }
                .add{
                        background: url("https://s10.mogucdn.com/img/x6/img/checkbox.png")  no-repeat;
                        background-size:0.36rem 0.8rem;
                        background-position: -0.02rem -0.01rem;
                        border:0px solid red;
                    }
              
                // border:1px solid red;
            }
            .right{
                width:90%;
                display:flex;
                font-size:0.24rem;
                font-weight:500;
                .img{
                    width:27%;
                    height:100%;
                    img{
                        width:78%;
                        height:100%;
                    }
                }
                .body{
                    width:70%;
                    height:100%;
                    p:nth-child(2){
                        margin-top:16%;
                        span:nth-child(1){
                            font-size: 0.35rem;
                        }
                        span:nth-child(2){
                            font-size: 0.2rem;
                            margin-left:5%;
                        }
                    }
                }
            }
        }
        .bottom{
            width:100%;
            height:0.8rem;
            display:flex;
            position:fixed;
            bottom:0;
            font-size:0.24rem;
            line-height: 0.8rem;

            li:first-child{
                width:10%;
                text-align:center;
                input{
                    -webkit-appearance: none;
                    width:0.36rem;
                    height:0.36rem;
                    border:1px solid gray;
                    border-radius: 0.18rem;
                    outline: none;
                }
                 .add{
                        background: url("https://s10.mogucdn.com/img/x6/img/checkbox.png")  no-repeat;
                        background-size:0.36rem 0.78rem;
                        background-position: -0.02rem -0.01rem;
                        border:0px solid red;
                    }
            }
            li:nth-child(2){
                width:20%;
                text-align:center;
            }
            li:nth-child(3){
                width:35%;

            }
            li:nth-child(4){
                width:30%;
                text-align:center;
                height:0.6rem;
                margin-top:0.1rem;
                background: rgb(248, 39, 39);
                border-radius: 0.05rem;
                input{
                    color:white;
                    
                }
                
            }
        }
          
    }
</style>





