<template>
    <div class="mrt-body">
        <div @click="addparams(v.title,v.link)" v-for="(v,i) of toplist" :key="i">
            <img :src="v.image">
            <p>{{v.title}}</p>
        </div>
    </div>
</template>

<script>
 export default {
        data(){
            return {
                
            }
        },
        watch:{
            // cid(){
            //     console.log(1111);
                 
            // }
        },
        computed:{
            toplist(){
                return  this.$store.getters.tops;
            },
            pid(){

            },
            cid(){
                return this.$store.getters.cid;
            }
        },
        methods:{
            addparams(title,link){
                var s=link.indexOf("=");
                var y=link.indexOf("&");
                var newstring=link.substr(s+1,y-s-1);
                console.log(newstring);
                this.$store.commit("setTitle",title);
                this.$store.commit("setCid",newstring);
                this.$store.dispatch("loadPointSearchs");
                this.$router.push({name:'searchpointdetails',params:{title:title}});

               


            }
        }
    }
</script>

<style lang="less">
    .mrt-body{
        display:flex;
        flex-wrap: wrap;
        margin-left:4%;
        div{
            width:29%;
            text-align: center;
            //   border:1px solid red;
              margin-left:1.7%;
            img{
                width:100%;
            }
            p{
                font-size:0.12rem;
                position: relative;
                top:-0.2rem;

            }
        }
    }
</style>




